body {
    background-color: #f7f7f7 !important;
}
.header Button{
    font-size: 20px;
    font-weight: 500;
}
.front_page{
    margin-top: 100px;
    padding: 15px;
}
.frontpage_name{
    font-size: 60px;
    font-weight: 600;
    line-height: 65px;
    margin-top: 10px;
    margin-bottom: 10px;
}
.frontpage_job{
    font-size: 26px;
    line-height: 34px;
    margin-bottom: 30px;
}
.about_img{
    display: inline-block;
    margin-top: 100px;
}
.about_me{
    margin: 100px 0 20px 0;
    font-size: 2rem;
    font-family: "roboto", sans-serif;
    font-weight: 500;
}
.about_skills{
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
    margin: 20px 0 30px 0;
}
.about_skills span{
    display: inline-block;
    margin: 0 10px 10px 0;
    padding: 5px;
    border: solid grey 1px;
}
.experience_heading_1{
    margin: 100px 0 40px 0;

}
.experience_heading span{
    display: inline-block;
    margin: 0 10px 10px 0;
    padding: 5px;
    border: solid grey 1px;
}
.experience_heading_2{
    margin-bottom: 40px;
}
.experience_heading_3{
    margin-bottom: 40px;
}
.front_page_UIbot{
    display: inline-flex;
    margin-top: 35px;
}
.front_page_UIbot img{
    padding: 2px;
    width: 117px;
    height: 117px;
    border-radius: 50%;
    border: 7px solid rgba(0, 0, 0, 0.07);
}
.botui.botui-container{
    background-color: #f7f7f7
}
.education{
    margin: 100px 0 20px 0;
}
.education_heading{
    font-size: 30px;
    line-height: 40px;
    font-weight: 500;
}

.front_page_UIbot .App .botui-container {
    max-height: 300px;
    overflow: scroll;
}